      h1 Administracin

    p.mb-5 Las organizaciones, en general, necesitan una administracin eficaz para alcanzar sus objetivos empresariales. Existen diferentes niveles de administracin, que tienen como fin organizar y coordinar las funciones comerciales de una empresa. La administracin debe considerar los intereses de todas las partes interesadas de la empresa, incluidos los empleados, los clientes y el gobierno. 

    .row.justify-content-center.mb-5
      .col-md-10.col-12
        .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-5
