            :style="{'background-image': `url(${require('@/assets/curso/tema1/img01.png')})`}"
          )(data-aos="fade-right")
          .bloque-texto-g__texto.p-4
            p.mb-0 Los administradores son responsables de establecer y lograr los objetivos de la organizacin. Por lo general, el objetivo principal de una organizacin consiste en lograr el crecimiento mediante la utilizacin de sus recursos humanos, materiales y financieros.
        p.mb-5 La administracin es el proceso de planificacin y organizacin de los recursos y actividades de una empresa para lograr objetivos especficos, de la manera ms eficaz y eficiente posible. Para mayor claridad, la figura expone en qu consiste cada uno de los conceptos involucrados.

        .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
          h5 Figura 1
          span Conceptos administrativos

        figure(data-aos="zoom-in")
          img(src='@/assets/curso/tema1/img02.svg')

    p.mb-5 Lo anterior permite, entonces, crear una organizacin dinmica, en la que la gerencia ayuda a su personal a adaptarse al cambio para que la organizacin contine manteniendo su ventaja competitiva. Este aspecto correspondiente a la importancia de la adaptacin puede llegar a significar la diferencia entre su xito y su fracaso. Por tal motivo, se debe ver la dinmica de la siguiente forma: 

    .row.justify-content-center.mb-5
      .col-md-10.col-12
        .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
          h5 Figura 2
          span Dinmica organizacional del logro

        figure(data-aos="zoom-in")
          img(src='@/assets/curso/tema1/img03.svg')

    p.mb-5 La administracin tambin posee algunas caractersticas fundamentales. 

    .tarjeta--container.row.mb-5
      .col-md.tarjeta.color-primario.p-5(data-aos="fade-right")
        .row.justify-content-center.mb-4
          .col-4
            figure
              img(src='@/assets/curso/tema1/img04.svg')
        h4.text-center Multidimensional
        p La mayora de la gerencia supervisa el servicio o el ciclo de produccin de una empresa u organizacin. Los gerentes trabajan en estrecha colaboracin y brindan orientacin a los miembros de su equipo. Un gerente considera a un miembro del personal como un individuo con diversas necesidades y como un componente del grupo ms grande. Para ser efectivos, los gerentes influyen en los miembros de su equipo para que apliquen sus fortalezas nicas para lograr las metas de la organizacin.
      .col-md.tarjeta.color-acento-contenido.p-5
        .row.justify-content-center.mb-4
          .col-4
            figure
              img(src='@/assets/curso/tema1/img05.svg')
        h4.text-center Dinmica
        p La administracin es una funcin dinmica que evoluciona y se adapta a los cambios de su entorno, ya sean econmicos, sociopolticos o tecnolgicos. 
        p Que la empresa pueda sobrevivir todava depende de la eficacia con la que su administracin pueda adaptarse a los nuevos requisitos del mercado.
      .col-md.tarjeta.color-acento-botones.p-5(data-aos="fade-left")
        .row.justify-content-center.mb-4
          .col-4
            figure
              img(src='@/assets/curso/tema1/img06.svg')
        h4.text-center Intangible
        p La gestin no es un producto tangible, pero su presencia puede cambiar la forma en que funciona una organizacin. La gestin consiste en ideologas, polticas e interaccin humana. Una buena gestin ayuda a mejorar los ndices de consecucin de los objetivos de una empresa, los niveles de satisfaccin de los empleados y la facilidad general en el funcionamiento de la empresa.

    p.mb-5 Ahora bien, dichas caractersticas se fundamentan en las funciones de la administracin, las cuales se explican a continuacin:

    TabsC.color-acento-botones.mb-5
      .py-3.py-md-4.px-4.BG02(titulo="Planeacin")
        .row.justify-content-center.align-items-center
          .col-md-5.col-12.mb-3
            figure
              img(src='@/assets/curso/tema1/img08.png')
          .col-md-7.col-12
            p La planeacin implica la creacin de una lnea de tiempo de las tareas que deben completarse para lograr un objetivo especfico. Los gerentes ejecutan la planeacin, que debe llevarse a cabo de manera sistemtica para evitar el desperdicio de recursos y tiempo. Un plan de accin detallado minimiza la confusin, el riesgo, el desperdicio y la incertidumbre. 
            .BG01.px-3.py-2
              p Por ejemplo, la alta direccin de una pequea empresa puede establecer un objetivo de ventas alto para un trimestre y as compensar las prdidas del trimestre anterior. El fundador de una empresa nueva puede planear hacer esfuerzos formales para asociarse con el gobierno y otras instituciones grandes para expandir la escala de sus operaciones.
      .py-3.py-md-4.px-4.BG02(titulo="Organizacin")
        .row.justify-content-center.align-items-center
          .col-md-5.col-12.mb-3
            figure
              img(src='@/assets/curso/tema1/img07.png')
          .col-md-7.col-12
            p El objetivo de la organizacin es fomentar una relacin simbitica entre el personal, los recursos financieros y fsicos de la empresa. La adecuada organizacin proporciona el curso de accin que cumple con todos los parmetros para el xito. La organizacin implica la identificacin y clasificacin de las actividades empresariales, la delegacin y la coordinacin. 
            .BG01.px-3.py-2
              p Por ejemplo, los altos directivos pueden asignar fondos o recursos a diferentes sucursales. Los gerentes de sucursal, luego, deben asignar fondos a los departamentos dentro de la sucursal, segn sus requisitos operativos. Posteriormente, los jefes de departamento realizan un seguimiento del gasto diario de los fondos.
      .py-3.py-md-4.px-4.BG02(titulo="Talento humano")
        .row.justify-content-center.align-items-center
          .col-md-5.col-12.mb-3
            figure
              img(src='@/assets/curso/tema1/img09.png')
          .col-md-7.col-12
            p La dotacin de personal implica reclutar y formar un equipo para la organizacin. El proceso de dotacin de personal de las empresas suele ser largo y profundo. 
            p La gerencia identifica los roles profesionales en la empresa y las habilidades/cualidades requeridas para desempearse bien en estos roles. Luego, el gerente selecciona al personal para esos roles a travs de los procesos de contratacin. Una vez seleccionados, los candidatos reciben formacin y se incorporan a la plantilla de la empresa. Los gerentes tambin son responsables de otorgar evaluaciones y promociones como parte de la dotacin de personal
      .py-3.py-md-4.px-4.BG02(titulo="Direccin")
        .row.justify-content-center.align-items-center
          .col-md-5.col-12.mb-3
            figure
              img(src='@/assets/curso/tema1/img10.png')
          .col-md-7.col-12
            p Dirigir implica dar los pasos necesarios para poner en marcha el trabajo y mantener la productividad para lograr los objetivos de la empresa, mediante acciones como supervisar, motivar y orientar a los miembros del personal; lo que es fundamental para las funciones de un gerente. 
            p Tambin requiere excelentes habilidades de liderazgo, comunicacin y relaciones interpersonales para impulsar al equipo hacia la consecucin de los objetivos de la organizacin.
      .py-3.py-md-4.px-4.BG02(titulo="Control")
        .row.justify-content-center.align-items-center
          .col-md-5.col-12.mb-3
            figure
              img(src='@/assets/curso/tema1/img11.png')
          .col-md-7.col-12
            p Esta funcin administrativa verifica que en cada nivel se evita la desviacin general de las especificaciones de calidad prescritas. 
            .BG01.px-3.py-2
              p Por ejemplo, los supervisores de una cadena de comida rpida a menudo microgestionan a los cajeros, el personal de cocina, el personal de entrega y los meseros, para asegurarse de que la comida y el servicio en su sucursal cumplan con los estndares de calidad. La alta direccin prescribe estos estndares de calidad y, por lo general, evolucionan con el tiempo.

    p.mb-5 Sumado a lo anterior, toda administracin necesita trazar una ruta de mando interno que permita establecer niveles, roles y responsabilidades, que a su vez se asumen para enfrentar el rumbo de la organizacin, teniendo en cuenta tanto sus caractersticas como sus funciones #[i per se]. Los niveles de la administracin se pueden clasificar en tres escalas: la alta direccin, el mando intermedio y la gestin operativa, tal como se aprecia en la figura.

    .row.justify-content-center.mb-5
      .col-md-10.col-12
        .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
          h5 Figura 3
          span Niveles administrativos

        ImagenInfograficaB.color-acento-botones
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/tema1/img12.svg')
          div(x="11.5%" y="69.9%" tooltip="Alta direccin")
            p.mb-5 La <b>alta direccin</b>, por lo general, est conformada por los ejecutivos ms importantes de la empresa, los cuales son:
            ul.lista-ul.mb-5
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | El presidente directivo
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | El director ejecutivo
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | El director de operaciones
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | El presidente y el vicepresidente
            p Su funcin radica en la integracin de diversos componentes de la empresa y la coordinacin de las actividades de los diferentes departamentos. Tambin analizan el entorno empresarial y sus implicaciones para formular objetivos con el fin de garantizar la supervivencia de la empresa y el bienestar de sus grupos de inters. 
          div(x="51.5%" y="87%" tooltip="Mando intermedio")
            p.mb-5 El <b>mando intermedio</b> vincula la gestin operativa con la alta direccin. Los jefes de divisin y/o departamento reciben orientacin de los gerentes superiores y son lderes para los gerentes operativos. Su trabajo es comprender las polticas enmarcadas por la alta direccin y transmitirlas a sus respectivas divisiones y/o departamentos para garantizar que cumplan con las polticas y decisiones de la empresa.
            ul.lista-ul.mb-5
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | Jefes de divisin
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | Jefes de departamento
          div(x="90%" y="75%" tooltip="Gestin operativa")
            p.mb-5 La <b>gestin operativa</b> est conformada por:
            ul.lista-ul.mb-5
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | Los supervisores 
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | Jefes de seccin
              li 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | Delegados 
            p Supervisan directamente los esfuerzos de la fuerza laboral. Son responsables del control de calidad y se aseguran de que el trabajo cumpla con los plazos. La alta direccin elabora los planes que definen la autoridad y responsabilidad de los supervisores.

