  .container.BGIMG01.px-4.px-md-5.pt-4
    #t_1_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.1	Objetivos 

    p.mb-5 Las empresas exitosas se basan tanto en metas como en objetivos, ya que aclaran el propsito de la empresa y ayudan a identificar las acciones necesarias. 
    
    .row.justify-content-center.mb-5
      .col-md-10.col-12
        .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
          h5 Figura 4
          span Metas y objetivos
        
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/tema1/img14.svg')
    p La gestin por objetivos est estrechamente relacionada con la evaluacin y revisin del desempeo; mide y juzga el desempeo individual con las metas de la organizacin, para aclarar tanto el trabajo a realizar como las expectativas de logro, y as fomentar la competencia, el crecimiento y motivacin del subordinado y la comunicacin entre partes. Adicionalmente a ello, busca servir como dispositivo de control e integracin organizacional.
  
  .container.container02.p-4.p-md-5
    p.mb-5 La administracin considera tener principalmente dos tipos de objetivos, en los cuales la gerencia debe tener presentes los intereses de todas las partes interesadas de la empresa, incluidos los empleados, los clientes y el gobierno. Estos son los objetivos organizacionales generales para cualquier empresa: 

    .row.justify-content-center.mb-5
      .col-lg-5.col-md-6.col-10.mb-3(data-aos="fade-right")
        .tarjeta.tarjeta-avatar-slide.BG03.p-5.h-100
          .tarjeta-avatar-slide__img.mb-4
            .tarjeta-avatar-slide__img__item: img.w-50(src='@/assets/curso/tema1/img15.svg')
            .tarjeta-avatar-slide__img__item: img.w-50(src='@/assets/curso/tema1/img17.svg')
          h5.mb-4 	Supervivencia:
          p Una organizacin necesita generar suficientes ingresos para cubrir sus costos operativos. 
      .col-lg-5.col-md-6.col-10.mb-3(data-aos="fade-left")
        .tarjeta.tarjeta-avatar-slide.BG03.p-5.h-100
          .tarjeta-avatar-slide__img.mb-4
            .tarjeta-avatar-slide__img__item: img.w-50(src='@/assets/curso/tema1/img16.svg')
            .tarjeta-avatar-slide__img__item: img.w-50(src='@/assets/curso/tema1/img18.svg')
          h5.mb-4 	Beneficio:
          p El beneficio proporciona un incentivo y es esencial para cubrir costos y riesgos sin precedentes asociados con la gestin de una empresa. 

    .cajon.color-secundario.p-4.mb-4(data-aos="fade-right")
      p  Aunque no son tan mencionados, <span style="color:#2B7E3B !important; font-weight: bold;">los objetivos sociales </span>se utilizan en cierto punto; la gerencia es responsable de crear beneficios para la sociedad a travs de su trabajo. Las empresas eligen hacer esto de diferentes maneras. Algunos pueden incorporar mtodos de produccin amigables con el medio ambiente, mientras que otros implementan salarios y oportunidades justos. Las empresas ms grandes, a menudo, mantienen o financian iniciativas que brindan servicios bsicos como atencin mdica y educacin. Segn la escala de sus operaciones, las empresas, a menudo, inician campaas de responsabilidad social.

    p Es vital que se creen objetivos especficos como parte de un plan de negocios. Establecer objetivos ayuda a impulsar a la empresa hacia el xito continuo y sigue siendo un punto de referencia incluso en tiempos difciles. 
    p Los objetivos de la empresa son medibles y describen efectivamente las acciones requeridas para realizar una tarea. Los objetivos tambin definen las tcnicas que utilizar la organizacin para lograr el xito de ventas, los estndares de servicio al cliente y las oportunidades de marca, as como cualquier otra aspiracin medible.
    p.mb-5 A continuacin, se describen los objetivos que se establecen para el desempeo y xito de la organizacin.

    .row.justify-content-center.mb-5
      .col-md-10.col-12
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row.justify-content-center.px-4(titulo="Objetivos de ventas")
            .col-md-4.col-8.p-0.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img19.png')
            .col-md-8
              p Ayudan a las empresas a medir su posicin frente a los competidores de la industria. Estos objetivos tienden a enfocarse en las formas en que una organizacin puede superar a la competencia en participacin de mercado, calidad del producto y reconocimiento de marca. 
              p Por ejemplo, si se posee una tienda de zapatos, un ejemplo de un objetivo de ventas podra ser <span style="color:#2B7E3B !important; font-weight: bold;">aumentar sus ventas mensuales en un 10 % durante el transcurso del ao</span>. Para hacer esto, es posible que deba implementar una estrategia de #[i marketing] para atraer nuevos compradores o introducir nuevos artculos que le permitan aumentar las ventas a sus clientes existentes. Su empresa debe contratar empleados talentosos, que puedan implementar sus decisiones y mantener la productividad y la eficiencia. 
          .row.justify-content-center.px-4(titulo="Objetivos de recursos")
            .col-md-4.col-8.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img20.png')
            .col-md-8
              p Cubren los objetivos de capacitacin y desarrollo de los empleados de la organizacin. Un objetivo de recursos humanos podra ser <span style="color:#2B7E3B !important; font-weight: bold;">reducir la rotacin de empleados en un 20 % mediante la introduccin de un nuevo programa de asistencia para empleados</span>. 
              p La dotacin de personal de una organizacin es tambin un foco de recursos humanos. Un objetivo para satisfacer las necesidades de personal podra ser la contratacin activa de trabajadores calificados en la organizacin. 
            p Uno de los objetivos que a menudo se pasa por alto para las pequeas empresas es garantizar que los empleados talentosos y capacitados permanezcan en la empresa. Cuando encuentre este tipo de trabajadores, su objetivo debe ser retenerlos ofrecindoles salarios competitivos, incentivos y un ambiente de trabajo que sea inclusivo y armonioso. Invertir en programas de capacitacin que brinden a sus empleados educacin adicional tambin puede ayudar a lograr este objetivo.
          .row.justify-content-center.px-4(titulo="Objetivos comerciales")
            .col-md-4.col-8.p-0.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img21.png')
            .col-md-8
              p Centrndose en el servicio al cliente, los gerentes comerciales establecen objetivos que se enfocan en brindar un servicio de calidad al cliente. Estos objetivos intentan <span style="color:#2B7E3B !important; font-weight: bold;">medir la satisfaccin del cliente con los costos y la calidad general de un producto o servicio</span>. Otro podra ser reducir el nmero y la frecuencia de las devoluciones y quejas de los clientes, o mejorar el tiempo de respuesta a las consultas de los clientes. Establecer conciencia de marca.
              p En la era del #[i marketing] en redes sociales, muchas pequeas empresas estn descubriendo que el #[i marketing] de marca es otro objetivo vital. La marca trata de vender la experiencia de su producto o servicio al comunicarle a su audiencia cmo su negocio satisface un deseo o necesidad y hace que sus vidas sean ms convenientes. Un objetivo de marca podra ser <span style="color:#2B7E3B !important; font-weight: bold;">aumentar su conocimiento de las redes sociales mediante el anlisis de una mtrica como las visitas al sitio web</span>.
            .BG01.p-4
              p Un objetivo comercial comn es ejecutar una operacin rentable, lo que generalmente significa aumentar los ingresos y limitar los gastos. Para alcanzar esta meta, los objetivos podran consistir en <span style="color:#2B7E3B !important; font-weight: bold;">aumentar las ventas anuales en un 10 % o conseguir tres nuevas cuentas cada mes</span>.
          .row.justify-content-center.px-4(titulo="Objetivos de <em>marketing</em>")
            .col-md-4.col-8.p-0.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img22.png')
            .col-md-8
              p Un plan de promocin describe las herramientas y tcticas que utilizar una empresa para lograr sus objetivos de #[i marketing]. Por ejemplo, si un objetivo de #[i marketing] es <span style="color:#2B7E3B !important; font-weight: bold;">crear conciencia de marca para un material de construccin entre los constructores de una ciudad especfica</span>, las herramientas y tcticas pueden incluir anuncios en las revistas de asociaciones locales de constructores de viviendas, patrocinar un evento al que asistan constructores u ofrecer descuentos promocionales a los constructores para que prueben el producto.
          .row.justify-content-center.px-4(titulo="Objetivos operativos")
            .col-md-4.col-8.p-0.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img23.png')
            .col-md-8
              p Los objetivos operativos son el desglose de las metas diarias, semanales o mensuales del proyecto en tareas factibles, para ayudar a la gerencia y al personal a lograr el plan estratgico de promocin general. 
              p Establecer los objetivos correctos es crucial para crear conciencia, atraer clientes potenciales y convertirlos en clientes. Al igual que con los objetivos estratgicos, los objetivos operativos de #[i marketing] deben ser cuantificables y especficos, pero con un enfoque ms limitado.
          .row.justify-content-center.px-4(titulo="Objetivos financieros")
            .col-md-4.col-8.p-0.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img24.png')
            .col-md-8
              p Generan rentabilidad y solvencia a la empresa, por ende, es fundamental que se diseen y se ejecuten para aumentar el rendimiento econmico de la empresa. Un ejemplo de un objetivo financiero podra ser el crecimiento de los ingresos y ganancias de la empresa. Adjuntar un nmero cuantificable, como un <span style="color:#2B7E3B !important; font-weight: bold;">crecimiento del 15 % en los ingresos y ganancias dentro de los prximos 12 meses</span>, es ideal porque puede medirse y ajustarse si es necesario.
              p Otro objetivo financiero podra centrarse en aumentar el capital y las inversiones, como <span style="color:#2B7E3B !important; font-weight: bold;">atraer nuevos accionistas e inversores mejorando la solvencia y el flujo de caja</span>. Aumento de las cifras de ventas.

    p.mb-5 Es importante establecer periodos de tiempo que sean realistas y puedan lograrse razonablemente. Adicionalmente a ello, deben redactarse claramente, usando palabras de accin. Estos son algunos ejemplos de objetivos comunes de toda organizacin.

    .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
      h5 Figura 5
      span Ejemplos de objetivos comunes

    figure(data-aos="zoom-in")
      img(src='@/assets/curso/tema1/img25.svg')

    p Finalmente, los objetivos deben basarse en un cronograma. Es posible que se requiera que el empleado logre los objetivos en tres meses, seis meses o antes de la prxima revisin de desempeo. 

