  .container.BGIMG02.px-4.px-md-5.pt-4  
    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.2. Planeacin estratgica

    .row.justify-content-center
      .col-md-6.col-12.mb-3
        p La planeacin estratgica es un proceso en el que los lderes de una organizacin definen su visin para el futuro e identifican las metas y objetivos de su organizacin. El proceso incluye establecer la secuencia en la que se deben realizar esos objetivos para que la organizacin pueda alcanzar su visin determinada. La planeacin estratgica generalmente representa objetivos a mediano y largo plazo, con una vida til de tres a cinco aos, aunque puede durar ms.
      .col-md-6.col-12.mb-3
        p El producto de la planeacin estratgica es un documento que puede ser fcilmente compartido, entendido y seguido por varias personas, incluidos empleados, clientes, socios comerciales e inversionistas. Un plan estratgico puede actualizarse y revisarse en ese momento para reflejar cualquier cambio estratgico, pues es una hoja de ruta para llegar a los objetivos comerciales. Los siguientes tres aspectos del desarrollo de la estrategia merecen atencin.
      .col-lg-10.col-12
