                    p <b>Comercial:</b> un plan estratgico centrado en el negocio se enfoca en los aspectos competitivos de la organizacin, creando ventajas competitivas y oportunidades de crecimiento. 
                    p Estos planes adoptan una misin que evala el entorno comercial externo, establece metas y asigna recursos financieros, humanos y tecnolgicos, para cumplir con esas metas.
