                    p.mb-5.mt-5 <b>Corporativo:</b> Un plan centrado en la empresa define cmo funciona la misma. Se enfoca en organizar y alinear la estructura del negocio, sus polticas, procesos y su liderazgo, para alcanzar las metas deseadas.
            .col-xl-4.col-lg-6.col-11.mb-lg-0.mb-3
