          p La planeacin estratgica puede mejorar los planes operativos y la eficiencia. Tambin ayuda a las organizaciones a limitar el tiempo dedicado a la gestin de crisis, donde reaccionan a cambios inesperados que no pudieron anticipar ni prepararse.

