    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.3. Modelos

    .row.justify-content-center.align-items-center
      .col-md-7.col-12.mb-3.order-md-1.order-1
        p Los modelos que se desarrollan tanto al interior como en lo externo de la organizacin examinan la orientacin de los procesos que perfeccionan el desempeo de los actores involucrados en la satisfaccin de las necesidades entre las partes. Son herramientas para ayudar a una empresa a desarrollar un plan de accin. El uso de modelos de estrategia ayudan en el proceso a analizar la estructura general. 
        p Los modelos son flexibles y pueden alertarlo sobre los ajustes necesarios. Se sugiere usarlos para competir con xito, hacer crecer un negocio y manejar con capacidad los diversos recursos econmicos, para mejorar la eficiencia productiva de una empresa.
      .col-md-5.col-12.mb-3.order-md-2.order-1
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/tema1/img41.png')

    .row.justify-content-center.mb-5
      .col-md-10.col-12
        .BG04.p-2
          p.mb-0.text-center Estos son algunos de los modelos a seguir: modelos administrativos, modelos de planeacin estratgica y modelos gerenciales.
    
    .row.mb-5
      .col-auto
        h4.ps-5.pb-2.mb-0.fondo1 Modelos administrativos

    p.mb-5  Un modelo administrativo describe la organizacin de una empresa y muestra cmo se gestiona y cmo se ve en el organigrama de una empresa. Un modelo administrativo es una estructura lgica, conformada por componentes que permiten organizar la direccionalidad y los recursos.
    p.mb-5 El personal se asignar a diferentes departamentos, que estarn bajo la jurisdiccin de personas de otros departamentos, que respondern a su vez a las divisiones de gestin, que, en ltima instancia, rendirn cuentas a la alta direccin. En algunos casos, la empresa puede dividirse an ms en sucursales o divisiones que residen en diferentes ubicaciones geogrficas, o se separan por motivos comerciales o polticos, debido a que la estructura y la cadena de mando de una empresa variarn frente a las dems; de ah la importancia de comprender el modelo administrativo que se utiliza.

    .row.justify-content-center.mb-5
      .col-lg-8.col-md-10.col-12
        .tarjeta-avatar-b
          .tarjeta-avatar-b__img
            img.imagen03.ms-md-3.ps-md-4(src='@/assets/curso/tema1/img43.svg' alt='AvatarTop')
          .tarjeta.tarjeta--blanco
            .py-4.pe-4 
              p.mb-0 En las empresas ms pequeas, por su parte, hay quienes toman las decisiones y quienes las siguen; esta estructura de mando se vuelve ms compleja a medida que la empresa crece.
          
    p Al observar los componentes que conforman la estructura lgica, es fcil ver la necesidad de comprender el modelo administrativo utilizado por una empresa. Al comprender cmo est organizada la empresa, puede aplicar ese conocimiento al diseo.  Hay varias formas en las que puede identificar el modelo actual utilizado por una empresa, estos elementos incluyen:

  .container.BGIMG04.px-4.px-md-5.pt-4  
    .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
      h5 Figura 8
      span Elementos para identificar el modelo administrativo
    figure.mb-5(data-aos="zoom-in")
      img(src='@/assets/curso/tema1/img45.svg')

    p.mb-5 Los organigramas sirven como un mapa de la estructura administrativa de la empresa, estos departamentos pueden tener divisiones que estn bajo su administracin, como en el caso de los departamentos de Nminas que estn bajo el departamento de Finanzas, por ejemplo. Al observar los componentes del diagrama de flujo, se puede ver que cada uno de estos podra convertirse en unidades organizativas individuales. Siguiendo este modelo, se podra crear fcilmente un diseo que funcione con el modelo existente que utiliza la empresa; entre los ms conocidos, se encuentran los siguientes.

    .row.justify-content-center.mb-5
      .col-lg-10.col-12
        .BG05.p-5
          SlyderA(tipo="b")
            .row.justify-content-center
              .col-lg-6.col-11
                h5.mb-5 Estructura jerrquica
                p Se refiere a la cadena de mando de una empresa, normalmente va desde la alta direccin y los ejecutivos hasta los empleados generales. 
                p Hay que tener en cuenta que es importante evaluar los pros y los contras, as como las necesidades de la empresa, para poder tomar la decisin ms beneficiosa para la organizacin.
              .col-lg-6.col-11
                figure
                  img(src='@/assets/curso/tema1/img46.svg')
            .row.justify-content-center
              .col-lg-6.col-11
                h5.mb-5 Estructura horizontal
                p Este modelo se usa en las empresas que estn iniciando su actividad econmica, puesto que muestra un orden jerrquico horizontal como columna vertebral de los procesos que se desarrollan en ella. 
                p Permite que los colaboradores puedan contar con lderes que orienten su trabajo, mas no se visualiza como jefe y subordinados que obedecen segn rdenes.
              .col-lg-6.col-11
                figure
                  img(src='@/assets/curso/tema1/img47.svg')
            .row.justify-content-center
              .col-lg-6.col-11
                h5.mb-5 Estructura de red
                p Este modelo relaciona todas las dependencias que forman parte de la organizacin, por lo que permite que la lnea de mando sea flexible, asumiendo la responsabilidad que recae sobre cada unidad administrativa. Cuando una empresa se entrega a una estructura de red, se dispersa ms.                
              .col-lg-6.col-11
                figure
                  img(src='@/assets/curso/tema1/img48.svg')
            .row.justify-content-center
              .col-lg-6.col-11
                h5.mb-5 Estructura de divisin
                p La estructura de divisin genera no solo unidades administrativas, sino oficinas productoras o equipos de trabajo que contribuyen a la realizacin de los objetivos de las dependencias principales; por tanto, cada subdivisin cuenta con lderes a cargo de estos equipos de trabajo. 
                p A su vez, se asume que hay una mxima autoridad que entrega jerarqua a estos lderes que orientan las divisiones, sin dejar de lado que esta mxima autoridad es quien analiza y toma las decisiones trascendentales de la organizacin.
              .col-lg-6.col-11
                figure
                  img(src='@/assets/curso/tema1/img49.svg')
            .row.justify-content-center
              .col-lg-6.col-11
                h5.mb-5 Estructura de lnea
                p Una estructura organizativa de lnea refleja la autoridad de arriba hacia abajo, con una cadena de mando establecida. Este tipo de estructura organizacional se observa a menudo en organizaciones que tienen tareas rutinarias.
              .col-lg-6.col-11
                figure
                  img(src='@/assets/curso/tema1/img50.svg')
            .row.justify-content-center
              .col-lg-6.col-11
                h5.mb-5 Estructura basada en equipos
                p En esta estructura organizacional, los empleados tienen ms autonoma y ms autoridad para tomar decisiones, sin necesidad de aprobacin gerencial, ya que permite comunicarse o intercambiar ideas en pro del desempeo laboral.
              .col-lg-6.col-11
                figure
                  img(src='@/assets/curso/tema1/img51.svg')

  .container.container02.px-4.px-md-5
    .row.mb-5
      .col-auto
        h4.ps-5.pb-3.mb-0.fondo1 Modelos de planeacin estratgica

    p Un modelo de planeacin estratgica es un trmino colectivo para varios elementos que contribuyen al proceso de planificacin estratgica. Se puede pensar en los modelos de planificacin estratgica como plantillas en las que puede colocar sus propias ideas. Al final, obtendr un plan estratgico que est estructurado de manera sensata y le brinda un conjunto claro de acciones sobre las cuales trabajar.
    p.mb-5 Los modelos de planeacin estratgica proporcionan una estructura general que aplica a su proceso de planificacin estratgica; es decir, los modelos de planificacin estratgica proporcionan una forma de estructurar la informacin de su estrategia y el contenido de su plan estratgico, los diversos componentes y cmo interactan entre s de manera coherente: lo que hacen, cmo encajan y en qu orden. 

    .row.justify-content-center.mb-5
      .col-lg-10.col-md-11.col-12.BGIMG05.py-4  
        .row.justify-content-center.align-items-center
          .col-md-11.col-12.px-4  
            p.text-center Ahora, una vez definido qu es realmente un modelo de planeacin estratgica, se deben tener en cuenta los elementos que la contienen: estructura y evaluacin.  
            .tarjeta-avatar-b.mb-5
              .tarjeta-avatar-b__img
                img.imagen01(src='@/assets/curso/tema1/img55.svg' alt='AvatarTop')
              .tarjeta.BG06
                .py-4.pe-4 
                  p.mb-0 La <b>estructura</b> se refiere a los diferentes componentes del plan estratgico y cmo encajan todos juntos. Por ejemplo, la estructura puede comenzar con la formulacin de la visin, luego fluir hacia valores, reas de enfoque y cualquier nmero de niveles de objetivos.
            .tarjeta-avatar-b.mb-5
              .tarjeta-avatar-b__img
                img.imagen01(src='@/assets/curso/tema1/img56.svg' alt='AvatarTop')
              .tarjeta.BG06
                .py-4.pe-4 
                  p.mb-0 La <b>evaluacin</b> se refiere a cmo har el seguimiento y la presentacin de informes sobre los elementos de la meta de su estrategia.
            p De igual forma, para su desarrollo, se hace necesario reconocer a fondo los siguientes aspectos de la organizacin.
      .col-lg-8.col-md-10.col-12.mb-5
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Figura 9
          span Aspectos organizacionales esenciales
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/tema1/img57.svg')
      .col-lg-10.col-md-11.col-12.BGIMG06.py-3
        .row.justify-content-center
          .col-md-11.col-12.px-4  
            p Independientemente de cul modelo de planeacin estratgica se elija como el ms apropiado para sus necesidades, existen mejores prcticas para asegurarse de que tenga xito. Por ejemplo, al hacer la investigacin, busque reunir un grupo que sea diverso, pero tambin apropiado para la meta, de tal forma que genere ms ideas al equipo de trabajo.

    p.mb-5 Cuando se tiene un plan, se debe ser realista. Si no puede ser ejecutado, entonces no se ha hecho bien el trabajo. Este debe ser accionable, con objetivos, tareas, responsabilidades delineadas, rendicin de cuentas y plazos claramente definidos, sin querer decir esto que no pueda ser flexible; los planes cambian, por lo que es mejor no ser rgido al respecto.

    .row.justify-content-center
      .col-md-4.col-8.mb-3
        figure(data-aos="zoom-in")
          img(src='@/assets/curso/tema1/img58.png')
      .col-md-8.col-12.mb-3
        p Si no se tiene un plan para alcanzar los objetivos, se est condenado al fracaso, lo que podra ser perder participacin de mercado o cerrar, ninguno de los cuales es un camino a seguir para una empresa viable. Cada organizacin es nica, por lo tanto, cada plan estratgico es nico, el objetivo aqu es brindarle una perspectiva sobre cmo puede abordar su planeacin antes de sumergirse en los detalles.
        p A continuacin, se exponen algunos modelos de planeacin estratgica, los cuales no son camisa de fuerza, puesto que la organizacin puede ser autora de su propio modelo de planeacin estratgica.

