          tr  
            td.text-center <b>Descripcin</b>
            td Requiere de la cooperacin de varias unidades administrativas para ejecutar dicho modelo, tal es el caso de las reas comerciales y de los ingenieros de sistemas que tienen como responsabilidad la seguridad de la informacin que produce la empresa. 
            td En l se establece la valoracin de las oficinas relacionadas con el cumplimiento de los objetivos que se describen en el plan estratgico, ejecutado a travs de comunicaciones asertivas que se apliquen a las estrategias planteadas.
            td Este modelo es sencillo y lo utilizan empresas pequeas para introducir la planeacin estratgica, no requiere de grandes volmenes de informacin, por ello es ideal para las empresas que quieren iniciar con un plan estratgico.
            td Este modelo de la planeacin estratgica apunta a desarrollar estrategias y objetivos basados en nuevos descubrimientos, como el estudio de factibilidad de un nuevo producto, competencias existentes en el mercado y los segmentos a quienes estar dirigido. Busca insertar su produccin en un mercado innovador.
            td Es el ms recomendado para las empresas consolidadas, ya que enfatiza su desarrollo en la ampliacin de los objetivos a largo plazo, haciendo exploracin de todo el contexto que rodea a la organizacin, sin dejar brechas que perjudiquen el cumplimiento de los cronogramas.
          tr 
            td.text-center <b>Aspectos para su desarrollo</b>
            td *Ejecucin de la estrategia de negocio.<br>*Herramientas tecnolgicas que aporten al desarrollo de las estrategias comerciales.<br>*Medir la competencia para lanzar nuevos productos y servicios.
            td *Indicadores de los objetivos.<br>*Evaluaciones de desempeo al personal.<br>*Controles financieros.<br>*Los planes de accin trazados para el desarrollo del plan estratgico. 
            td *Misin de la organizacin.<br>*Objetivos a alcanzar.<br>*Estrategias y actividades para cumplir con los objetivos.<br>*Control e informes de resultados sobre la planeacin estratgica.
            td *Mercados para crecer.<br>*Capturar nueva demanda.<br>*Productos nicos.<br>*No haya competencia.
            td *Anlisis DOFA (debilidades, oportunidades, fortalezas y amenazas).<br>*Misin.<br>*Planeacin.<br>*Presupuesto.<br>*Cronograma.<br>*Monitoreo o control de los avances.

    .row.mb-5
      .col-auto
        h4.ps-5.pb-3.mb-0.fondo1 Modelos gerenciales

    p.mb-5 Este tipo de modelo, al igual que los anteriores, tiene una clasificacin especfica, la cual est enfocada, como bien lo indica, en procesos gerenciales de una organizacin. Dependiendo del logro a alcanzar, se implementa alguno de estos modelos.

    ImagenInfograficaB.imagen04.color-acento-botones.mb-5
      template(v-slot:imagen)
        figure
          img(src='@/assets/curso/tema1/img60.svg')
      div(x="16.3%" y="72%" )
        .row.justify-content-center.align-items-center
          .col-md-11
            h5 Calidad total
            p Los colaboradores de bajo nivel hasta los ejecutivos de ms alto rango se concentran en mejorar la calidad y, por lo tanto, brindar servicios al cliente dejando como resultado la satisfaccin.
            p Se centra en las mejoras de procesos a largo plazo, en lugar de simplemente enfatizar las ganancias financieras a corto plazo.
      div(x="33.4%" y="72%" )
        .row.justify-content-center.align-items-center
          .col-md-11
            h5 Kaizen
            p Cuando se aplica al lugar de trabajo, significa una mejora continua que involucra a todos, tanto a los gerentes como a los trabajadores, implica hacer que el ambiente de trabajo sea ms eficiente y eficaz, creando una atmsfera de equipo, mejorando los procedimientos cotidianos y asegurando el compromiso de los empleados.
      div(x="50.6%" y="72%" )
        .row.justify-content-center.align-items-center
          .col-md-11
            h5 Justo a tiempo
            p Las empresas emplean esta estrategia para aumentar la eficiencia y disminuir el desperdicio. Se aplica justo a tiempo a travs de un sistema de inventario que alinea los pedidos de materias primas de los proveedores directamente con los programas de produccin; tambin reduce los costos de inventario. 
            p Este mtodo requiere que los productores pronostiquen la demanda con precisin, solo cuando la necesiten para el proceso de produccin, reduciendo los costos de inventario. 
      div(x="68%" y="72%" )
        .row.justify-content-center.align-items-center
          .col-md-11
            h5 Reingeniera
            p La ejecucin adecuada de la reingeniera puede cambiar las reglas del juego para cualquier negocio. Si se maneja adecuadamente, la reingeniera de procesos de negocios puede hacer milagros en una empresa en quiebra o estancada, aumentando las ganancias e impulsando el crecimiento.
            p La productividad de los empleados definitivamente se ve afectada durante la reingeniera de procesos; si los procesos se gestionan mejor durante el tiempo de ejecucin, la necesidad de reingeniera se reduce considerablemente.
      div(x="84.7%" y="72%" )
        .row.justify-content-center.align-items-center
          .col-md-11
            h5 #[i Benchmarking]
            p Tiene como objetivo proponer un modelo de evaluacin comparativa, que se puede aplicar para todos los tipos de mediciones. Es necesario evaluar los puntos fuertes y dbiles de la operacin interna. 
            p Solo se estar preparado para diferenciar sus posibilidades en el mercado si se conoce la fuerza y debilidad de las competencias.

    separador

    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.4. Recursos

    p.mb-5 Cuando se comienza un negocio, es posible que se haya pensado que todo se trataba de los ingresos que puede obtener, qu tan grande puede llegar a ser y en qu piso estar la oficina. Los recursos organizacionales se combinan, usan y transforman en productos terminados durante el proceso de produccin.

    .row.justify-content-center
      .col-md-10.col-12.mb-3
        .bloque-texto-c.color-secundario.p-4.mb-5
          i.fas.fa-quote-right
          h4.mb-2 Desde el punto de vista de Peter Drucker, en el escrito de Snchez Murillo (2006), los gerentes dan direccin a sus organizaciones, brindan liderazgo y deciden cmo usar los recursos de la organizacin para lograr las metas.
        p.mb-5 A continuacin, se describen los tipos de recursos existentes y necesarios en una organizacin para posicionarse en el mercado:
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row.justify-content-center.px-4(titulo="Recursos humanos")
            .col-md-4.col-8.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img61.png')
            .col-md-8
              p Son las personas que trabajan para una organizacin. Sus habilidades y conocimientos son invaluables para los gerentes. La actitud de los expertos debe administrar la organizacin y saber trabajarla de manera efectiva. Enfatiza la oportunidad de aplicar el conocimiento y la necesidad de aprender y crecer al hacerlo, el conocimiento no tiene sentido a menos que haya la oportunidad de aplicarlo y esto se logra a travs de la exposicin.
              p La capacitacin, por otro lado, es un esfuerzo organizacional destinado a ayudar a un empleado a adquirir las habilidades bsicas requeridas para la ejecucin efectiva y eficiente de las funciones para las que ha sido contratado.
            p El desarrollo de la mano de obra es tpicamente una parte de la estrategia de recursos humanos de la organizacin y tiene como objetivo maximizar el potencial del capital humano para alcanzar los objetivos comerciales estratgicos 
          .row.justify-content-center.px-4(titulo="Recursos monetarios o financieros")
            .col-md-4.col-8.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img62.png')
            .col-md-8
              p Estos, a menudo, incluyen efectivo, crdito y lneas de crdito, junto con la capacidad de tener opciones de planes de acciones para los empleados de la organizacin. La mayora de las empresas tienen recursos financieros de manera regular, pero pocas empresas tendrn recursos financieros extremadamente slidos, como los bancos, que se basan completamente en recursos financieros y los utilizan como un recurso clave.
            p La funcin de la administracin es planificar, organizar, dotar de personal, dirigir y controlar. Cada una de estas funciones est influenciada en gran medida por la cantidad de dinero que hay. Los gerentes y el personal del programa simplemente no pueden llevar a cabo sus responsabilidades asignadas de manera efectiva sin comprender sus limitaciones financieras. Los gerentes deben tener algn medio para saber lo que est sucediendo con respecto a sus recursos financieros si van a tomar decisiones gerenciales informadas. Necesitan hacer un seguimiento de cmo se gasta el dinero, un informe que dicte cunto dinero haba, cunto se gast, en qu se gast y cunto queda. 
            p Esta responsabilidad se lleva a cabo instalando y administrando un sistema de contabilidad financiera, se dispone de una serie de programas informticos aceptables para satisfacer esta necesidad contable. Por lo general, es innecesario para la mayora de las organizaciones dedicar tiempo y recursos a disear e implementar un sistema nico. 
          .row.justify-content-center.px-4(titulo="Recursos de capital o de produccin")
            .col-md-4.col-8.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img63.png')
            .col-md-8
              p Las materias primas son los elementos utilizados directamente para la fabricacin de productos, proporcionando un suministro adecuado de materiales al menor costo posible; es decir, se ocupa de las materias primas, la adquisicin de mquinas y otros equipos necesarios para el proceso de produccin, y repuestos para el mantenimiento de la planta. 
              p Comienza con la determinacin de la calidad y cantidad de los materiales, y finaliza con su envo a produccin para satisfacer la demanda del cliente, segn el cronograma y al menor costo.  
          .row.justify-content-center.px-4(titulo="Recursos comerciales")
            .col-md-4.col-8.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img64.png')
            .col-md-8
              p Los recursos comerciales son de naturaleza no fsica e intangible, como las patentes del producto, las marcas de la organizacin, los datos del cliente y el conocimiento del cliente, e incluso el talento en la organizacin como una forma de recurso intelectual. 
              p Una vez que se desarrollan, pueden ofrecer ventajas nicas a la organizacin, como ofrecer descuentos y ofertas especiales para clientes habituales o compras repetidas del mismo cliente. Los tipos de recursos comerciales afectarn la sostenibilidad de la organizacin junto con su rentabilidad. 
          .row.justify-content-center.px-4(titulo="Recursos fsicos")
            .col-md-4.col-8.mb-md-0.mb-4
              figure
                img(src='@/assets/curso/tema1/img65.png')
            .col-md-8
              p Estos se consideran activos tangibles que la organizacin utiliza para crear una oferta de valor y una propuesta para sus clientes. Los recursos fsicos pueden incluir el equipo, los edificios, el inventario, la planta de fabricacin y la red de distribucin, que son extremadamente cruciales para que el negocio funcione correctamente. Los recursos fsicos son importantes para el funcionamiento de la organizacin, ya que, sin cosas como equipo, inventario y planta de fabricacin es difcil que la empresa funcione.
            p Son las mquinas utilizadas durante el proceso de fabricacin. Las mquinas modernas pueden mejorar en gran medida la eficiencia del proceso de fabricacin. Si una organizacin usa maquinaria vieja y obsoleta, es posible que no pueda competir con una organizacin que use maquinaria ms eficiente.  La gestin de materiales es la planificacin, direccin, control y coordinacin de aquellas actividades relacionadas con los requisitos de materiales e inventario, desde el punto de su inicio hasta su introduccin en el proceso de fabricacin.
      .col-md-8.col-10.mb-3.BG07.p-3
        p.mb-0 Los recursos que son importantes para la organizacin y crean una propuesta de valor en el servicio a sus clientes, y adems le entregan el producto, se denominan recursos clave. Estos tipos de recursos comerciales son cruciales y es importante tenerlos para que su negocio tenga xito. Suelen basarse en recursos tangibles e intangibles.

    separador

