    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.5 Planeacin

    p.mb-5 Aqu es donde se pronosticarn los objetivos y metas que debe cumplir la empresa y los mtodos a llevar a cabo. Tambin contribuye a asignar eficientemente los recursos. Entre sus premisas, se encuentran:

    .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
      h5 Figura 10
      span Premisas de la planeacin
    figure.mb-5(data-aos="zoom-in")
      img(src='@/assets/curso/tema1/img67.svg')

    .row.justify-content-center.mb-5
      .col-md-10.col-12
        p.mb-5 En una organizacin, la planeacin de la gestin es el establecimiento de una estrategia que permite alcanzar un conjunto de objetivos preestablecidos. Dentro del proceso de planeacin, se pueden identificar los siguientes pasos en los que se involucran algunas actividades especficas.
        .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
          h5 Figura 11
          span Proceso de planeacin
        figure.mb-5(data-aos="zoom-in")
          img(src='@/assets/curso/tema1/img68.svg')
      .col-md-6.col-12
        p Los principios de la planeacin, por su parte, se pueden describir de esta manera:

    SlyderF.mb-5(columnas="col-lg-6 col-xl-4")
      .tarjeta-avatar
        img.imagen02(src='@/assets/curso/tema1/img69.svg' alt='AvatarTop')
        .tarjeta.color-primario
            .p-5
              p <b>Inherencia:</b> toda organizacin debe contar con un plan que determine cmo resolver determinados problemas y as alcanzar los objetivos de la forma ms eficiente posible.
      .tarjeta-avatar
        img.imagen02(src='@/assets/curso/tema1/img70.svg' alt='AvatarTop')
        .tarjeta.color-primario
            .p-4
              p.mb-0 <b>Racionalidad:</b> consiste en comprender un problema para luego determinar los criterios de diseo del plan, el establecimiento de alternativas y su aplicacin. 
              p Las decisiones deben tomarse con base en la lgica y la razn, no de acuerdo con las emociones y los valores.
      .tarjeta-avatar
        img.imagen02(src='@/assets/curso/tema1/img71.svg' alt='AvatarTop')
        .tarjeta.color-primario
            .p-5
              p.mb-0 <b>Unidad:</b> se asignan objetivos que, a su vez, debern orientarse hacia una meta comn; si bien existe un plan para cada una de las funciones, todas deben unirse en un plan principal.
      .tarjeta-avatar
        img.imagen02(src='@/assets/curso/tema1/img72.svg' alt='AvatarTop')
        .tarjeta.color-primario
            .p-5
              p.mb-0 <b>Compromiso:</b> para llegar a la meta, se debe calcular el tiempo que implica el compromiso de cada recurso.
      .tarjeta-avatar
        img.imagen02(src='@/assets/curso/tema1/img73.svg' alt='AvatarTop')
        .tarjeta.color-primario
            .p-5
              p.mb-0 <b>Exactitud:</b> el plan debe ser exacto, cualquier error puede afectar el resto de funciones de la organizacin, cuanto ms precisos los planes, mayores sern las posibilidades de lograr los objetivos.
      .tarjeta-avatar
        img.imagen02(src='@/assets/curso/tema1/img74.svg' alt='AvatarTop')
        .tarjeta.color-primario
            .p-5
              p.mb-0 <b>Factibilidad:</b> todo plan debe ser realista: diseado con base en la experiencia, hechos concretos y recursos disponibles.            

    p.mb-5 Ahora bien, con base en toda la descripcin previamente dada sobre la planeacin, se destacan ciertos elementos que la componen de manera transversal. 
  
  .container.container02.px-4.px-md-5
    .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
      h5 Figura 12
      span Elementos que componen la planeacin
    figure.mb-5(data-aos="zoom-in")
      img(src='@/assets/curso/tema1/img75.svg')

    p.mb-5 Existen diferentes tipos de planeacin: estratgica, tctica y operacional.

    .row.justify-content-center.mb-5
      .col-md-10.col-12
        AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row.justify-content-center.px-4(titulo="Estratgica")
            .col-md-4.col-6.mb-4
              figure
                img(src='@/assets/curso/tema1/img76.png')
            .col-md-8
              p Es una planeacin a largo plazo, realizada por los directivos de la organizacin. Analiza cmo inciden los factores externos e internos en los objetivos establecidos: campaas publicitarias, presencia en los medios, incursin en el mercado, etc.
              p La planeacin estratgica es el proceso de la empresa de definir objetivos, misiones y valores organizacionales (incluida su direccin a largo plazo), para comprender mejor los recursos y el presupuesto que necesita asignar para garantizar la eficacia del plan. Esto puede incluir definir qu acciones deben tomarse para lograr esos objetivos desde un alto nivel.
            p El plan estratgico se ejecuta de arriba hacia abajo; adems, su alcance es limitado y puede cambiar anualmente con nfasis en el mapeo de actividades de rutina.
          .row.justify-content-center.px-4(titulo="Tctica")
            .col-md-4.col-6.mb-4
              figure
                img(src='@/assets/curso/tema1/img77.png')
            .col-md-8
              p Son los planes que estn diseados para enfrentar una crisis y por eso estn ligados a decisiones de corto plazo que responden al plan estratgico. La planeacin tctica es un tipo de planificacin que implica dividir un plan estratgico a largo plazo en planes a corto plazo. Las empresas y los equipos suelen utilizar este tipo de plan cuando tienen objetivos a largo plazo que se extienden ms all de dos o tres aos. 
              p Hay varios elementos clave de un plan tctico:
            .BG01.p-3.mb-4
              ul.lista-ul
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Los objetivos especficos deben ser a corto plazo, que se puedan lograr en un ao o menos. 
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Las tcticas no tienen plazos, sino que simplemente estn ah para respaldar su capacidad de alcanzar cada objetivo.
                li.mb-0  
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Las acciones son pasos especficos o tareas que deben completarse para acercarse a la finalizacin de una meta. 
                li.mb-0  
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Contemplar los recursos necesarios para ejecutar las acciones que permitan alcanzar los objetivos trazados.
                li.mb-0  
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Cada tarea o accin que forma parte del plan tctico debe tener una fecha lmite.
            p.mb-4 Beneficios de un plan tctico
            ul.lista-ul.mb-5
              li.mb-0  
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | Ayuda a convertir las estrategias en acciones que, en ltima instancia, contribuyen a una empresa o equipo a alcanzar sus objetivos.
              li.mb-0 
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | Fomenta un mejor rendimiento, ya que los empleados y los miembros del equipo tendrn pasos prcticos para alcanzar los objetivos.
              li.mb-0  
                <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                | Permite a los departamentos y equipos priorizar su trabajo, y garantiza que cada tarea se vincula con la estrategia general. 
          .row.justify-content-center.px-4(titulo="Operacional")
            .col-md-4.col-6.mb-4
              figure
                img(src='@/assets/curso/tema1/img78.png')
            .col-md-8
              p Este plan aborda la forma en que se organizan los recursos (incluidos los empleados) para la resolucin de conflictos. Un plan de operaciones resume cmo crear y entregar su producto o servicio a sus clientes. Los tipos de problemas operativos que enfrentar variarn segn el tipo de negocio que opere. En la mayora de las empresas, suceden muchas cosas adems del negocio principal de proporcionar productos o servicios a los clientes. 
              p La planeacin operativa es lo que sucede cuando un equipo o departamento se basa en un plan estratgico de toda la empresa orientado al futuro: traza los presupuestos y objetivos del departamento para impulsar el xito del plan estratgico con actividades especficas basadas en el equipo para los prximos 1 a 3 aos.
            p Los planes operativos funcionan mejor cuando todo un departamento participa, asigna fechas de vencimiento para tareas, mide objetivos para el xito, informa sobre problemas y colabora de manera efectiva. 

    p El beneficio principal de la planificacin operativa es que se puede ver el efecto de sus operaciones en el resultado final del negocio en tiempo real, o en cada punto de referencia, para que se sepa exactamente cundo actuar. De ah, la importancia de que est tan personalizado para cada departamento, pues es as como se sabe exactamente dnde fallan las cosas y por qu.
    p Dado que los planes operativos se construyen en un esfuerzo por asignar fondos, recursos y personal para cada periodo de 1 a 3 aos, todos los pasos que debe incluir un plan operativo deberan servir, en ltima instancia, para ese esfuerzo. A continuacin, se describe cada uno de sus pasos.

    .row.justify-content-center
      .col-md-10
        PasosA.color-acento-botones.mb-5(tipo="l")
          div
            h4 <span style="background-color:#03386E !important; color:#fff; padding: 2px 5px;">1</span> Visualizar el plan operativo
            p Las caractersticas principales de un plan operativo incluyen tareas para lograr objetivos particulares y claramente definidos, adems de la gestin de su personal para garantizar que funcionen a niveles ptimos. Identifique su visin en lo que respecta a esos niveles y luego podr comenzar con la fase de investigacin.
          div
            h4 <span style="background-color:#03386E !important; color:#fff; padding: 2px 5px;">2</span> Investigar e identificar metas
            p Para comenzar a desarrollar su plan operativo, comience por examinar sus objetivos, es decir, con qu presupuesto cuenta y rendicin de cuentas con respecto a vigencias anteriores. Aborde esto desde una perspectiva presupuestaria, una perspectiva de recursos y desde la perspectiva de un miembro del equipo. Hay que pensar en esta poca el prximo ao, dentro de dos aos, dentro de tres aos, etc.
            p Esto puede incluir plazos de lanzamiento de productos, cantidad de productos fabricados, cantidad de casos de servicio al cliente cerrados, cantidad de reseas de 5 estrellas recibidas, cantidad de clientes adquiridos, ingresos aumentados en un cierto porcentaje, etc.
          div
            h4 <span style="background-color:#03386E !important; color:#fff; padding: 2px 5px;">3</span> Asignar presupuestos y personas
            p Una vez que haya trazado sus objetivos, es importante tener en cuenta que el presupuesto de su plan operativo proviene del presupuesto anual de su departamento. Entonces, dicho esto, el presupuesto es lo ms importante a considerar al comenzar a asignar tareas, recursos y presupuesto para los miembros del equipo.
          div
            h4 <span style="background-color:#03386E !important; color:#fff; padding: 2px 5px;">4</span> Informe sobre el plan operativo
            p Una vez que se haya trazado el plan operativo, que debe incluir objetivos claros con entregables, metas, cronogramas y el personal necesario para lograr el plan, hay que desarrollar un proceso para informar sobre la ejecucin del plan.

    .cajon.color-secundario.p-4.mb-4
      p.mb-0 A modo de resumen, entonces, se pueden sintetizar los tipos de planeacin de acuerdo con su periodo y amplitud, tal como se percibe en la siguiente tabla. Esto es interesante en el sentido que permite saber qu tipo de planeacin se podra implementar de acuerdo con la necesidad que tenga la empresa en ese momento.

    .row.justify-content-center.mb-5
      .col-md-6.col-12.mb-3
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 2
          span Tipos de planeacin

        table.mb-5(data-aos="fade-right")
          thead
            tr
              th(style="background-color: #3AE3FE; color: #000").text-center.py-2 PLANEACIN
              th(style="background-color: #3AE3FE; color: #000").text-center.py-2 PERIODO
              th(style="background-color: #3AE3FE; color: #000").text-center.py-2 AMPLITUD
          tbody
