      h2 1.6. Cronogramas

    p.mb-5 Un cronograma de actividades o programa de trabajo est diseado para un perodo de tiempo especfico, en el que se pretende que se lleven a cabo las actividades o acciones determinadas. El cronograma entregar los siguientes resultados:

