        p Al hacer un cronograma de actividades o programa de trabajo, hay tres cosas que se deben considerar: preparar, hacer y concluir. A continuacin, se describen los pasos para un programa de trabajo.

    ImagenInfografica.color-acento-botones.mb-5
      template(v-slot:imagen)
        figure
