      .tarjeta.BG02.p-3(x="68%" y="79%" numero="+")
        p Cada actividad debe completarse dentro de un marco de tiempo. Se puede establecer un lmite para cada una de las actividades y responsabilidades, para que los involucrados en la ejecucin de las actividades las desarrollen en el tiempo estipulado.
      .tarjeta.BG02.p-3(x="84.6%" y="79%" numero="+")
        p Una vez que haya diseado su plan de programacin de actividades, se debe mantener su ejecucin y finalizacin dentro del marco de tiempo establecido. Mantener las actividades le ayudar a obtener un resultado efectivo en la planeacin.

