    #t_1_7.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.7. Diagrama de causa y efecto

    p El diagrama de causa y efecto es una herramienta grfica que permite a un equipo identificar, categorizar y examinar las posibles causas relacionadas con un problema. La intencin es exponer las causas raz ms probables para una mayor investigacin.
    p Los diagramas de causa y efecto son una herramienta til cuando se trabaja para identificar las causas fundamentales de un problema durante una investigacin de causas fundamentales con un equipo. El diagrama de causa y efecto examina por qu sucedi o podra suceder algo al organizar las causas potenciales en categoras ms pequeas. Tambin puede ser til para mostrar las relaciones entre los factores contribuyentes. Se observa grficamente, ya que ilustra la relacin entre un resultado dado y todos los factores que influyen en l.
    p Hay tres beneficios significativos al usar un diagrama de causa y efecto con un equipo.

    .titulo-sexto.color-acento-contenido.mb-5(data-aos="fade-right")
      h5 Figura 14
      span Beneficios del diagrama causa y efecto
    figure.mb-5(data-aos="zoom-in")
      img(src='@/assets/curso/tema1/img87.svg')

    p Asimismo, este diagrama cuenta con  una estructura especfica y un proceso de elaboracin determinado, tal como se aprecia en el siguiente interactivo.

  .container.container02.px-4.px-md-5
    .row.justify-content-center
      .col-md-10.col-12.mb-5
        ImagenInfograficaB.imagen04.color-acento-botones.mb-5
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/tema1/img88.svg')
          .tarjeta.BG02.p-3(x="87.6%" y="69%" numero="+")
            p El recuadro con el enunciado del problema se ubica a la derecha, aprovechando el tiempo de avance para reforzar las causas, que ocurren antes de que suceda el problema.<br><br> Hay que escribir un enunciado claro del problema (ttulo o resumen). Incluya elementos como quin, qu, cundo, dnde y cunto. Sea especfico y asegrese de que todos comprendan completamente el enunciado del problema.
          .tarjeta.BG02.p-3(x="18%" y="17%" numero="+")
            p Agregue de cuatro a seis ramas primarias que sean apropiadas para el diagrama, y enumere las categoras principales para las posibles causas.
          .tarjeta.BG02.p-3(x="31.7%" y="38%" numero="+")
            p.mb-0 Capture notas sobre la informacin adicional necesaria, experimentos potenciales, etc., para no ralentizar ni distraer el proceso de enumerar las causas. Trabaje para resolver el problema y revise el diagrama a medida que haya nueva informacin disponible o las causas iniciales investigadas no hayan conducido a una solucin para el problema. Las causas inferiores pueden no ser obvias o ser menos probables que otras.
            p Es una sesin de lluvia de ideas, por lo tanto, abstenerse de criticar o juzgar las causas como se sugieren. Hay tiempo ms tarde para filtrar las sugerencias, pero esas sugerencias pueden desencadenar la lnea de pensamiento que conduce a una solucin.
          .tarjeta.BG02.p-3(x="79.4%" y="28.5%" numero="+")
            p El diagrama debe ser visible para todo el equipo involucrado en el trabajo del problema.
      .col-md-10.col-12.mb-5.BG09.p-4
        LineaTiempoC.color-secundario(text-small)
          .row.justify-content-center(titulo="Paso 1")
            .col-md-10
              p <b> Paso No. 1. </b> Decidir sobre el efecto a examinar. El efecto se expresa como una cualidad particular, a travs de caractersticas, problemas derivados del trabajo, objetivos de planificacin, etc.
              ul.lista-ul
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Desarrolle una definicin operativa del efecto y asegurarse de que se entienda claramente.
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Recuerde, el efecto puede ser positivo (un objetivo) o negativo (un problema), dependiendo del tema que se est discutiendo.
          .row.justify-content-center(titulo="Paso 2")
            .col-md-10
              p <b> Paso No. 2. </b> Utilizar o seleccionar una herramienta de grficos colocada de manera que todos puedan verla. Luego, hay que realizar el diagrama de esta manera:
              ul.lista-ul
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Dibujar una flecha horizontal que apunte hacia la derecha (esta es la columna vertebral).
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | A la derecha de la flecha, escribir una breve descripcin del efecto o resultado que surge del proceso.
          .row.justify-content-center(titulo="Paso 3")
            .col-md-10
              p <b> Paso No. 3. </b> Identificar las causas principales que contribuyen al efecto que se est estudiando. Estas son las etiquetas para las principales ramas del diagrama y se convierten en categoras bajo las cuales enumerar las muchas causas relacionadas con las mismas. Luego de etiquetar, hay que trazar una lnea diagonal para formar una rama que conecta la categora con la columna vertebral.
          .row.justify-content-center(titulo="Paso 4")
            .col-md-3.col-6
              figure
                img(src='@/assets/curso/tema1/img89.svg')
            .col-md-7
              p <b> Paso No. 4. </b> Identificar tantas causas o factores como sea posible y adjuntarlos como subramas de las ramas principales.
              ul.lista-ul
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Si una causa menor se aplica a ms de una causa mayor, hay que anotar ambas. 
          .row.justify-content-center(titulo="Paso 5")
            .col-md-3.col-6
              figure
                img(src='@/assets/curso/tema1/img90.svg')
            .col-md-7
              p <b> Paso No. 5. </b> Identificar niveles de causas cada vez ms detallados y continuar con el diseo del diagrama. Las causas se pueden organizar mediante la formulacin de la pregunta por qu.
          .row.justify-content-center(titulo="Paso 6")
            .col-md-10
              p <b> Paso No. 6. </b> Analizar el diagrama para identificar las causas que justifican el problema. Dado que los diagramas de causa y efecto identifican solo posibles causas, hay que buscar las causas que aparecen repetidamente, pues estas pueden representar causas fundamentales. Tambin es necesario buscar aquellas que se pueden medir y cuantificar para posibles cambios que se realicen. 
              p Este ltimo paso es crucial, ya que invita a analizar en equipo el resultado del diagrama de causa y efecto. En esta etapa, se debe tener un diagrama que muestre todas las posibles causas del problema que se les ocurran. Esto puede implicar la exploracin e indagacin, la realizacin de encuestas y entrevistas, adems de evaluaciones diseadas para probar cul de estas posibles causas est realmente contribuyendo al problema.
          .row.justify-content-center(titulo="Beneficios")
            .col-md-10
              ul.lista-ul
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Identifica las posibles causas raz, las razones bsicas, para un efecto especfico, problema o condicin.
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Ordena y relaciona algunas de las interacciones entre los factores que afectan un proceso o situacin particular.
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Analiza los problemas existentes para que se puedan tomar acciones correctivas.
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Indaga sobre problemas de mantenimiento con las herramientas utilizadas o la cantidad de herramientas disponibles.
                li.mb-0 
                  <i class="fas fa-circle fa-xs" style="color:#359547!important;"></i>
                  | Es flexible y adaptable. Se puede usar tambin en problemas con materias primas, tiempo, transporte, calidad de los suministros, entre otros.

      .col-md-8.col-12.mb-5.BG07.p-3
        p Por ltimo, una vez que se haya completado el diagrama, examine la informacin tal como se ha organizado para llegar a una solucin y crear elementos de accin.


